.input-text__wrapper {
    width: 100%;
    position: relative;
}

.input-list {
    @include reset-list;
}

.input-item {
    position: relative;
    padding-top: 24px;
}

.input-item__label {
    position: absolute;
    top: 0;
    left: 0;
}

.input-text {
    width: 100%;
    padding: 14px 10px;
    padding-right: 33px;
    border: 2px solid #444;

    &::placeholder {
        color: #444;
        opacity: 0.3;
    }

    &:hover,
    &:active,
    &:focus {
        outline: none;
        border: 2px solid $color-active;
    }
}