html {
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

html, body {
    height: 100%;
}

body {
    display: flex;
    flex-direction: column;
    min-width: $minWidth;
    margin: 0;
    font-family: 'Open Sans', Arial, sans-serif;
    font-size: 14px;
    line-height: 1.375em;
    color: #333;
    background-color: #fff;

    &.lock {
        overflow: hidden;
    }
}

a {
    color: #444;
    text-decoration: none;
}

img {
    max-width: 100%;
    height: auto;
}

h2 {
    font-size: 25px;
    line-height: 30px;
}

h3 {
    font-size: 17px;
    line-height: 24px;
}

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.page-content {
    flex-basis: auto;
    flex-grow: 1;
    flex-shrink: 0;
    overflow-x: hidden;
}

.container {
    max-width: $maxWidthContainer;
    margin: 0 auto;
    @media (max-width: $width-xl) {
        max-width: 970px;
    }
    @media (max-width: $max-width-lg) {
        max-width: 750px;
    }
    @media (max-width: $max-width-md) {
        max-width: none;
        padding: 0 10px;
    }
}
