.page-header {
    padding: 20px 0;
}

.page-header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.main-nav__toggle {
    display: none;
}

.main-nav__list {
    @include reset-list;

    display: flex;
    justify-content: space-between;
}

.main-nav__item {
    padding-right: 30px;
    
    &:last-child {
        padding-right: 0;
    }
}

@media (min-width: $minWidth) and (max-width: $max-width-md) {
    .main-nav__list {
        width: 100%;
        height: 100%;
        padding: 60px 10px 20px;
        position: fixed;
        top: 0;
        left: -200%;
        background-color: red;
        transition: .3s ease;
        z-index: 100;
    }

    .main-nav--active .main-nav__list   {
        display: block;
        left: 0;
    }

    .main-nav__toggle {
        display: inline-block;
        width: 34px;
        height: 16px;
        padding: 0;

        position: relative;

        background-color: transparent;
        border: none;
        cursor: pointer;

        z-index: 110;

        &-line {
            width: 100%;
            height: 2px;
            margin-top: -1px;
            position: absolute;
            top: 50%;
            left: 0;
            background-color: #221f1f;
            transition: all 0.3s ease 0s;
        }

        &::before,
        &::after {
            content: '';
            width: 100%;
            height: 2px;
            position: absolute;
            left: 0;
            background-color: #221f1f;
            transition: all 0.3s ease 0s;
        }

        &::before {
            top: 0;
        }

        &::after {
            bottom: 0;
        }

        &:focus {
            outline-offset: 6px;
        }
    }

    .main-nav--active .main-nav__toggle {
        &-line {
            transform: scale(0);
        }

        &::after {
            transform: rotate(45deg);
            top: 50%;
        }

        &::before {
            transform: rotate(-45deg);
            top: 50%;
        }
    }

    .main-nav__link {
        font-size: 18px;
        line-height: 1.4;
    }
}