.checkbox-list {
    @include reset-list;
}

.input-checkbox + .checkbox-item__label {
    display: block;
    padding: 9px 0;
    padding-left: 54px;
    position: relative;
    cursor: pointer;
    user-select: none;

    &::before {
        content: '';
        width: 40px;
        height: 40px;
        position: absolute;
        top: 0;
        left: 0;
        border: 2px solid #444;
    }
}

.input-checkbox:checked + .checkbox-item__label::after {
    content: '';
    width: 22px;
    height: 16px;
    position: absolute;
    top: 13px;
    left: 10px;
    background-image: url('../images/icons/check.svg');
    background-size: cover;
}

@media (min-width: $min-width-md) {
    .input-checkbox:focus + .checkbox-item__label::before {
        border: 2px solid $color-active;
    }

    .input-checkbox + .checkbox-item__label:hover::before {
        border: 2px solid $color-active;
    }

    .input-checkbox + .checkbox-item__label:active::before {
        border: 2px solid #ccc;
    }
}

