.radio-list {
    @include reset-list;
}

.input-radio + .radio-item__label {
    display: block;
    padding: 9px 0 9px 54px;
    position: relative;
    cursor: pointer;

    &::before {
        content: '';
        width: 40px;
        height: 40px;
        position: absolute;
        top: 0;
        left: 0;
        border: 2px solid #444;
        border-radius: 50%;
    }
}

.input-radio:checked + .radio-item__label::after {
    content: '';
    width: 16px;
    height: 16px;
    border-radius: 50%;
    position: absolute;
    top: 12px;
    left: 12px;
    background-color: $color-active;
}

@media (min-width: $min-width-md) {
    .input-radio:focus + .radio-item__label::before {
        border: 2px solid $color-active;
    }

    .input-radio + .radio-item__label:hover::before {
        border: 2px solid $color-active;
    }

    .input-radio + .radio-item__label:active::before {
        border: 2px solid #ccc;
    }
}
