.input-textarea {
    width: 100%;
    min-height: 142px;
    padding: 18px;
    border: 2px solid #444;
    color: #444;
    font-size: 17px;
    line-height: 18px;
    resize: none;

    &::placeholder {
        color: #444;
        opacity: 0.3;
    }

    &:hover,
    &:active,
    &:focus {
        outline: none;
        border: 2px solid $color-active;
    }
}