.modal__title {
    margin: 0;
    text-align: center;
    text-transform: uppercase;
    font-size: 30px;
    line-height: 38px;
}

.modal__sub-title {
    font-weight: 400;
}

.modal__form {
    width: 100%;
}

.modal__input-list {
    min-width: 200px;
    margin-bottom: 30px;
}

@media (max-width: $max-width-md) {
    .modal__title {
        font-size: 22px;
    }
}

