article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden],
template {
  display: none;
}

html {
  background: #fff;
  color: #444;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

html,
button,
input,
select,
textarea {
  font-family: sans-serif;
}

body {
  margin: 0;
}

a {
  background: transparent;
}

a:focus {
  outline: thin dotted;
}

a:hover,
a:active {
  outline: 0;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

h2 {
  font-size: 1.5em;
  margin: 0.83em 0;
}

h3 {
  font-size: 1.17em;
  margin: 1em 0;
}

h4 {
  font-size: 1em;
  margin: 1.33em 0;
}

h5 {
  font-size: 0.83em;
  margin: 1.67em 0;
}

h6 {
  font-size: 0.75em;
  margin: 2.33em 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

mark {
  background: #ff0;
  color: #444;
}

code,
kbd,
pre,
samp {
  font-family: monospace, serif;
  font-size: 1em;
}

pre {
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
}

q {
  quotes: "\201C" "\201D" "\2018" "\2019";
}

q:before,
q:after {
  content: '';
  content: none;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

figure {
  margin: 0;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  padding: 0;
  white-space: normal;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
}

button,
input {
  line-height: normal;
}

button,
select {
  text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type="search"] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans Regular"), local("OpenSans-Regular"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Open Sans Bold"), local("OpenSans-Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin: 0;
  font-family: 'Open Sans', Arial, sans-serif;
  font-size: 14px;
  line-height: 1.375em;
  color: #333;
  background-color: #fff;
}

body.lock {
  overflow: hidden;
}

a {
  color: #444;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

h2 {
  font-size: 25px;
  line-height: 30px;
}

h3 {
  font-size: 17px;
  line-height: 24px;
}

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-content {
  flex-basis: auto;
  flex-grow: 1;
  flex-shrink: 0;
  overflow-x: hidden;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
}

.btn {
  display: inline-block;
  padding: 13px 40px;
  position: relative;
  color: #444;
  font-size: 17px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  background-color: #fff;
  border: 2px solid #444;
  cursor: pointer;
  transition: all .2s ease;
}

.btn--fw {
  width: 100%;
}

.btn-close {
  width: 18px;
  height: 18px;
  background-image: url("../images/icons/close.svg");
  position: absolute;
}

.radio-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.input-radio + .radio-item__label {
  display: block;
  padding: 9px 0 9px 54px;
  position: relative;
  cursor: pointer;
}

.input-radio + .radio-item__label::before {
  content: '';
  width: 40px;
  height: 40px;
  position: absolute;
  top: 0;
  left: 0;
  border: 2px solid #444;
  border-radius: 50%;
}

.input-radio:checked + .radio-item__label::after {
  content: '';
  width: 16px;
  height: 16px;
  border-radius: 50%;
  position: absolute;
  top: 12px;
  left: 12px;
  background-color: #62d1ba;
}

.checkbox-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.input-checkbox + .checkbox-item__label {
  display: block;
  padding: 9px 0;
  padding-left: 54px;
  position: relative;
  cursor: pointer;
  user-select: none;
}

.input-checkbox + .checkbox-item__label::before {
  content: '';
  width: 40px;
  height: 40px;
  position: absolute;
  top: 0;
  left: 0;
  border: 2px solid #444;
}

.input-checkbox:checked + .checkbox-item__label::after {
  content: '';
  width: 22px;
  height: 16px;
  position: absolute;
  top: 13px;
  left: 10px;
  background-image: url("../images/icons/check.svg");
  background-size: cover;
}

.input-text__wrapper {
  width: 100%;
  position: relative;
}

.input-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.input-item {
  position: relative;
  padding-top: 24px;
}

.input-item__label {
  position: absolute;
  top: 0;
  left: 0;
}

.input-text {
  width: 100%;
  padding: 14px 10px;
  padding-right: 33px;
  border: 2px solid #444;
}

.input-text::placeholder {
  color: #444;
  opacity: 0.3;
}

.input-text:hover,
.input-text:active,
.input-text:focus {
  outline: none;
  border: 2px solid #62d1ba;
}

.input-textarea {
  width: 100%;
  min-height: 142px;
  padding: 18px;
  border: 2px solid #444;
  color: #444;
  font-size: 17px;
  line-height: 18px;
  resize: none;
}

.input-textarea::placeholder {
  color: #444;
  opacity: 0.3;
}

.input-textarea:hover,
.input-textarea:active,
.input-textarea:focus {
  outline: none;
  border: 2px solid #62d1ba;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.form {
  padding: 50px 0;
}

.form__title {
  text-align: center;
}

.form__fieldset {
  margin: 0 0 30px;
  padding: 20px 0 40px;
}

.form__fieldset--clear {
  border: none;
}

.form__legend {
  text-align: center;
  font-weight: 700;
  position: relative;
}

.form__list {
  display: flex;
  justify-content: center;
}

.form__item {
  margin-right: 30px;
}

.form__input-text {
  min-width: 300px;
}

.form__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cards__title {
  text-align: center;
}

.cards__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.cards__item-wrapper {
  flex: 0 0 33.333%;
  margin-bottom: 30px;
  padding: 0 15px;
}

.card {
  background-color: #fff;
  border: 1px solid #dce1e6;
  box-shadow: 0px 4px 10px rgba(90, 90, 90, 0.04);
}

.card__image {
  width: 100%;
  height: 260px;
  border-bottom: 1px solid #dce1e6;
}

.card__image img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card__info {
  padding: 20px 10px;
}

.card__title {
  margin: 0 0 20px 0;
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
}

.card__link {
  color: blue;
}

.card__descr {
  margin: 0;
  font-size: 14px;
}

.modal__title {
  margin: 0;
  text-align: center;
  text-transform: uppercase;
  font-size: 30px;
  line-height: 38px;
}

.modal__sub-title {
  font-weight: 400;
}

.modal__form {
  width: 100%;
}

.modal__input-list {
  min-width: 200px;
  margin-bottom: 30px;
}

.modal {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.9);
  overflow-y: auto;
  overflow-x: hidden;
  transition: all .2s ease;
  visibility: hidden;
  opacity: 0;
  z-index: 100;
}

.modal.modal--active {
  opacity: 1;
  visibility: visible;
}

.modal__body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100%;
  transition: all .2s ease;
}

.modal__content {
  width: 100%;
  max-width: 568px;
  padding: 50px 110px;
  position: relative;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  background-color: #fff;
  transition: all .2s ease;
  transform: translate(0, -100%);
  opacity: 0;
}

.modal.modal--active .modal__content {
  transform: translate(0, 0);
  opacity: 1;
}

.modal__btn-close {
  position: absolute;
  top: 20px;
  right: 20px;
}

.section-tabs__tabs {
  max-width: 600px;
  margin: 0 auto;
  padding: 40px 0;
}

.tabs__nav {
  display: flex;
}

.tabs-nav__btn {
  margin-right: 20px;
}

.tabs-nav__btn:last-child {
  margin-right: 0;
}

.tabs-nav__btn--active {
  background-color: #62d1ba;
  color: #fff;
}

.tabs__content {
  margin: 0;
  padding: 0;
  list-style: none;
  padding: 20px 0;
}

.tabs-content__item {
  display: none;
}

.tabs-content__item--active {
  display: block;
}

.section-spollers__spollers {
  max-width: 600px;
  margin: 0 auto;
  padding: 40px 0;
}

.spollers__item {
  margin-bottom: 20px;
}

.spollers__item:last-child {
  margin-bottom: 0;
}

.spollers-item__btn {
  display: inline-block;
  width: 100%;
  padding: 13px 50px;
  position: relative;
  color: #444;
  font-size: 17px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  background-color: #fff;
  border: 2px solid #444;
  cursor: pointer;
  transition: all .2s ease;
}

.spollers-item__btn::before,
.spollers-item__btn::after {
  content: "";
  width: 20px;
  background-color: #444;
  height: 2px;
  transition: all 0.3s ease;
  position: absolute;
  top: 50%;
}

.spollers-item__btn::before {
  transform: translate(0, -50%) rotate(-45deg);
  right: 15px;
}

.spollers-item__btn::after {
  transform: translate(0, -50%) rotate(45deg);
  right: 29px;
}

.spollers-item__btn--active::before {
  transform: translate(0, -50%) rotate(45deg);
}

.spollers-item__btn--active::after {
  transform: translate(0, -50%) rotate(-45deg);
}

.spollers-item__content {
  display: none;
  margin: 0;
  padding-top: 20px;
}

.page-header {
  padding: 20px 0;
}

.page-header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-nav__toggle {
  display: none;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
}

.main-nav__item {
  padding-right: 30px;
}

.main-nav__item:last-child {
  padding-right: 0;
}

.page-footer {
  padding: 30px 0;
  background-color: #f1f1f1;
}

@media (min-width: 768px) {
  .btn:hover,
  .btn:hover {
    background-color: #444;
    color: #fff;
    outline: none;
  }

  .btn:active {
    background-color: #62d1ba;
    color: #fff;
  }

  .btn-close:hover,
  .btn-close:focus {
    opacity: 0.5;
  }

  .btn-close:active {
    opacity: 0.2;
  }

  .input-radio:focus + .radio-item__label::before {
    border: 2px solid #62d1ba;
  }

  .input-radio + .radio-item__label:hover::before {
    border: 2px solid #62d1ba;
  }

  .input-radio + .radio-item__label:active::before {
    border: 2px solid #ccc;
  }

  .input-checkbox:focus + .checkbox-item__label::before {
    border: 2px solid #62d1ba;
  }

  .input-checkbox + .checkbox-item__label:hover::before {
    border: 2px solid #62d1ba;
  }

  .input-checkbox + .checkbox-item__label:active::before {
    border: 2px solid #ccc;
  }

  .card__link:hover {
    color: red;
  }

  .spollers-item__btn:hover,
  .spollers-item__btn:focus {
    background-color: #444;
    color: #fff;
  }

  .spollers-item__btn:hover::before,
  .spollers-item__btn:hover::after,
  .spollers-item__btn:focus::before,
  .spollers-item__btn:focus::after {
    background-color: #fff;
  }
}

@media (max-width: 1182px) {
  .container {
    max-width: 970px;
  }
}

@media (max-width: 991.98px) {
  .container {
    max-width: 750px;
  }
}

@media (max-width: 767.98px) {
  .container {
    max-width: none;
    padding: 0 10px;
  }

  .modal__title {
    font-size: 22px;
  }
}

@media (max-width: 575.98px) {
  .modal__content {
    max-width: 320px;
    padding: 36px 30px 46px 30px;
  }
}

@media (min-width: 320px) and (max-width: 767.98px) {
  .main-nav__list {
    width: 100%;
    height: 100%;
    padding: 60px 10px 20px;
    position: fixed;
    top: 0;
    left: -200%;
    background-color: red;
    transition: .3s ease;
    z-index: 100;
  }

  .main-nav--active .main-nav__list {
    display: block;
    left: 0;
  }

  .main-nav__toggle {
    display: inline-block;
    width: 34px;
    height: 16px;
    padding: 0;
    position: relative;
    background-color: transparent;
    border: none;
    cursor: pointer;
    z-index: 110;
  }

  .main-nav__toggle-line {
    width: 100%;
    height: 2px;
    margin-top: -1px;
    position: absolute;
    top: 50%;
    left: 0;
    background-color: #221f1f;
    transition: all 0.3s ease 0s;
  }

  .main-nav__toggle::before,
  .main-nav__toggle::after {
    content: '';
    width: 100%;
    height: 2px;
    position: absolute;
    left: 0;
    background-color: #221f1f;
    transition: all 0.3s ease 0s;
  }

  .main-nav__toggle::before {
    top: 0;
  }

  .main-nav__toggle::after {
    bottom: 0;
  }

  .main-nav__toggle:focus {
    outline-offset: 6px;
  }

  .main-nav--active .main-nav__toggle-line {
    transform: scale(0);
  }

  .main-nav--active .main-nav__toggle::after {
    transform: rotate(45deg);
    top: 50%;
  }

  .main-nav--active .main-nav__toggle::before {
    transform: rotate(-45deg);
    top: 50%;
  }

  .main-nav__link {
    font-size: 18px;
    line-height: 1.4;
  }
}