.btn {
    display: inline-block;
    padding: 13px 40px;
    position: relative;
    color: #444;
    font-size: 17px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    background-color: #fff;
    border: 2px solid #444;
    cursor: pointer;
    transition: all .2s ease;
}

.btn--fw {
    width: 100%;
}

.btn-close {
    width: 18px;
    height: 18px;
    background-image: url('../images/icons/close.svg');
    position: absolute;
}

@media (min-width: $min-width-md) {
    .btn:hover,
    .btn:hover {
        background-color: #444;
        color: #fff;
        outline: none;
    }
    
    .btn:active {
        background-color: $color-active;
        color: #fff;
    }

    .btn-close:hover,
    .btn-close:focus {
        opacity: 0.5;
    }
    
    .btn-close:active {
        opacity: 0.2;
    }
}


