
@import "./form/button.scss";
@import "./form/radio.scss";
@import "./form/checkbox.scss";
@import "./form/input.scss";
@import "./form/textarea.scss";

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.form {
    padding: 50px 0;
}

.form__title {
    text-align: center;
}

.form__fieldset {
    margin: 0 0 30px;
    padding: 20px 0 40px;
}

.form__fieldset--clear {
    border: none
}

.form__legend {
    text-align: center;
    font-weight: 700;
    position: relative;
}

.form__list {
    display: flex;
    justify-content: center;
}

.form__item {
    margin-right: 30px;
}

.form__input-text {
    min-width: 300px;
}

.form__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
}