.section-tabs__tabs {
    max-width: 600px;
    margin: 0 auto;
    padding: 40px 0;
}

.tabs__nav {
    display: flex;
}

.tabs-nav__btn {
    margin-right: 20px;

    &:last-child {
        margin-right: 0;
    }
}

.tabs-nav__btn--active {
    background-color: $color-active;
    color: #fff;
}

.tabs__content {
    @include reset-list;

    padding: 20px 0;
}

.tabs-content__item {
    display: none;
}

.tabs-content__item--active {
    display: block;
}