.cards__title {
    text-align: center;
}

.cards__list {
    @include reset-list;

    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
}

.cards__item-wrapper {
    flex: 0 0 33.333%;
    margin-bottom: 30px;
    padding: 0 15px;
}

.card {
    background-color: #fff;
    border: 1px solid #dce1e6;
    box-shadow: 0px 4px 10px rgba(90, 90, 90, 0.04);
}

.card__image {
    width: 100%;
    height: 260px;
    border-bottom: 1px solid #dce1e6;
}

.card__image img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.card__info {
    padding: 20px 10px;
}

.card__title {
    margin: 0 0 20px 0;
    font-size: 18px;
    line-height: 24px;
    font-weight: 700;
}

.card__link {
    color: blue;
}

.card__descr {
    margin: 0;
    font-size: 14px;
}

@media (min-width: $min-width-md) {
    .card__link:hover {
        color: red;
    }
}