.section-spollers__spollers {
    max-width: 600px;
    margin: 0 auto;
    padding: 40px 0;
}

.spollers__item {
    margin-bottom: 20px;

    &:last-child {
        margin-bottom: 0;
    }
}

.spollers-item__btn {
    display: inline-block;
    width: 100%;
    padding: 13px 50px;
    position: relative;
    color: #444;
    font-size: 17px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    background-color: #fff;
    border: 2px solid #444;
    cursor: pointer;
    transition: all .2s ease;

    &::before,
    &::after {
        content: "";
        width: 20px;
        background-color: #444;
        height: 2px;
        transition: all 0.3s ease;
        position: absolute;
        top: 50%;
    }

    &::before {
        transform: translate(0, -50%) rotate(-45deg);
        right: 15px;
    }
    &::after {
        transform: translate(0, -50%) rotate(45deg);
        right: 29px;
    }
}

.spollers-item__btn--active {
    &::before {
        transform: translate(0, -50%) rotate(45deg);
    }
    &::after {
        transform: translate(0, -50%) rotate(-45deg);
    }
}

.spollers-item__content {
    display: none;
    margin: 0;
    padding-top: 20px;
}

@media (min-width: $min-width-md) {
    .spollers-item__btn:hover,
    .spollers-item__btn:focus {
        background-color: #444;
        color: #fff;

        &::before,
        &::after {
            background-color: #fff;
        }
    }
}

