@import "./modal/order.scss";

.modal {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(255, 255, 255, 0.9);
    overflow-y: auto;
    overflow-x: hidden;
    transition: all .2s ease;
    visibility: hidden;
    opacity: 0;
    z-index: 100;
}

.modal.modal--active {
    opacity: 1;
    visibility: visible;
}

.modal__body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100%;
    transition: all .2s ease;
}

.modal__content {
    width: 100%;
    max-width: 568px;
    padding: 50px 110px;
    position: relative;
    border: 1px solid #c6c6c6;
    box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
    background-color: #fff;
    transition: all .2s ease;
    transform: translate(0, -100%);
    opacity: 0;
}

.modal.modal--active .modal__content {
    transform: translate(0, 0);
    opacity: 1;
}

.modal__btn-close {
    position: absolute;
    top: 20px;
    right: 20px;
}

@media (max-width: $max-width-sm) {
    .modal__content {
        max-width: $minWidth;
        padding: 36px 30px 46px 30px;
    }
}